/* Import required modules
-------------------------------------------------- */
@import '../sass/utils';
@import '../sass/variables';
@import '../sass/custom-fancytree';
@import '../sass/custom-data-table';
@import '../sass/custom-vartical-tabs';
$breadcrumbColor:#8a8a8a;
$primaryHighlightColor:#0f69ca;

@mixin onlyForMobile {
    @media screen and (max-width: 767px) {
        @content
    }
}

@mixin mobileAndUp {
    @media screen and (min-width: 767px) {
        @content
    }
}

@mixin lwTransition($time:0.3s, $property:all) {
    -webkit-transition  : $property $time ease-in-out;
    -moz-transition     : $property $time ease-in-out;
    -ms-transition      : $property $time ease-in-out;
    -o-transition       : $property $time ease-in-out;
    transition          : $property $time ease-in-out;
}

/*
 * Base structure
 */

/* Move down content because we have a fixed navbar that is 50px tall */
body {
  padding: 0;
  overflow-x: initial;
   /* Margin bottom by footer height */
  // margin-bottom: 60px;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;

  a, .page-link {
    color: $primaryHighlightColor;
    outline: 0;
  }

  .lw-btn-xs {
    padding: 2px 8px;
    font-size: 0.8em;
  }

    .btn-primary {
        background-color: $primaryHighlightColor;
        border-color: $primaryHighlightColor;
        color: #ffffff!important;
    }

    .btn-outline-*{
        background-color: #fff;
    }

    .btn-outline-primary {
        color: $primaryHighlightColor;
        background-color: #fff;
        // background-color: transparent;
        background-image: none;
        border-color: $primaryHighlightColor;
    }

    .btn-outline-primary:hover {
        color: #fff;
        background-color: $primaryHighlightColor;;
        border-color: $primaryHighlightColor;;
    }

  .page-item.active .page-link {
    background-color: $primaryHighlightColor;
    border-color: $primaryHighlightColor;
  }
}

html {
    overflow-y:scroll;
    position: relative;
    min-height: 100%;

    .lw-disabling-block .lw-processing-window {
        display:none;
    }
}

*, html, body {
