@keyframes lwNgnDrop {
  from {
    top: -500px;
  }
  to {
    top: 126px;
  }
}

/* Notifications */
div.ngn {
    z-index: 99999;
    padding: 10px 25px;
    position: fixed;
    width: 40%;
    left: 30%;
    top: 126px;
    color:#fff;
    border-radius: 4px;
    animation-name: lwNgnDrop;
    animation-duration: 0.3s;
    text-align: center;

    @include onlyForMobile() {
        width: 90%;
        left: 5%;
    }

    .ngn-dismiss {
        box-shadow: none;
        color: #fff;
        opacity: 1; 
        text-shadow: 0px 0px 5px transparent;
        top: 5px;
        // right: -12px;
        background: transparent;
        border-radius: 0;

        &:active,
        &:hover {
            background-color: transparent;
        }
    }
}

